@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@700&display=swap);
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0; 

}

body { 
  font-family: 'Merriweather', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  background-color: #282c34;
  max-width: 1100px;
  margin: 0 auto;
}

a{
  text-decoration: none;
  color: lightgray;
}

.App{padding-left:20px;text-align:left}.App .won-card{background-color:#4da375}.App .lose-card{background-color:#a34d5d}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;margin-bottom:20px;padding-left:10px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-details{text-align:right;padding-right:10px}.MatchDetailCard .additional-details>p{margin-bottom:20px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:10px;row-gap:10px}.TeamPage .home-page{padding-top:20px;grid-column:span 1}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:flex-end}.TeamPage .team-name{font-size:2rem;margin-bottom:20px}.TeamPage .win-lose-section{justify-self:center;padding:20px;text-align:center}.TeamPage .match-details-section{grid-column:span 5}.TeamPage .more{-webkit-align-self:center;align-self:center;justify-self:center;font-size:larger}

.MatchPage{display:grid;grid-template-columns:1fr 4fr;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px}.MatchPage .year-selection{justify-self:end;padding:20px}.MatchPage .match-page-heading{margin:20px}.MatchPage .Team-not-played{text-align:100px}

.year-list{text-align:left;list-style-type:circle}.year-list li{margin-bottom:10px;margin-right:10px}

.Team-Title{height:100px;text-align:center;border:1px solid white;padding:20px}

.HomePage .app-name{font-size:3rem;text-align:center;margin:30px auto}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-row-gap:10px;grid-column-gap:10px}

